import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from "../store/index"
// 解决vue-router版本过高      在router 包升级后 login页面在跳转的时候抛出了一个promise异常 没有接收它 报错的问题
const a = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return a.call(this, location, onResolve, onReject);
  return a.call(this, location).catch(err => err);
}

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    meta:{title:'',showNav:true},
    component: Home
  },
  {
    path: '/category',
    name: 'Category',
    meta:{title:'',showNav:true},
    component: () => import('../views/Category.vue')
  },
  {
    path: '/cart',
    name: 'Cart',
    meta:{title:'',showNav:true},
    component: () => import('../views/Cart.vue')
  },
  {
    path: '/my',
    name: 'My',
    meta:{isLogin:true,title:'',showNav:true},
    component: () => import('../views/My.vue')
  },
  {
    path: '/search',
    name: 'Search',
    meta:{title:'',showNav:true},
    component: () => import('../views/Search.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/goodlist/:id',
    name: 'GoodList',
    props:true,
    meta:{title:'',showNav:true},
    component: () => import('../views/GoodList.vue')
  },
  {
    path: '/detail/:id',
    name: 'Detail',
    props:true,
    component: () => import('../views/Detail.vue')
  },
  {
    path: '/orders/submit/:id',
    name: 'Submit',
    props:true,
    component: () => import('../views/Submit.vue')
  },
  {
    path: '/add_address',
    name: 'Add_address',
    props:true,
    component: () => import('../views/Add_address.vue')
  },
  {
    path:"/orders/status/:id/:num",
    component: () => import('@/views/Status'),
  },
  {
    path: '/user/add_manage',
    name: 'Add_manage',
    props:true,
    component: () => import('../views/Add_manage.vue')
  },
    {
    path: '/collection',
    name: 'Collection',
    props:true,
    component: () => import('../views/Collection')
  },
  {
    path:'*',
    name:"NotFount",
    meta:{title:'',showNav:true},
    component: () => import('../views/NotFount.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
})
//路由守卫
router.beforeEach((to,from,next)=>{
  if(to.meta.isLogin){
    if(!store.getters.token){
      next('/login')
    }
  }
  next();
})
export default router
