import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    token: null,
    userInfo: null,
    submitMsg:[]
  },
  getters: {
    token(state) {
      let t = state.token
      if (!t) {
        t = localStorage.getItem("token")
      }
      return t
    },
    userInfo(state) {
      let t = state.userInfo
      if (!t) {
        t = JSON.parse(localStorage.getItem("userInfo")) 
      }
      return t
    }
  },
  mutations: {
    login(state, payload) {
      state.token = payload;
      localStorage.setItem('token', payload);//本地存储token数据
      document.cookie = "token=" + payload;//cookie存储token数据
    },
    loginUser(state,payload) {
      state.userInfo=payload;
    },
    loginOut(state){
      state.token=null;
      state.userInfo=null;
      localStorage.removeItem('userInfo'),
      localStorage.removeItem('token'),
      document.cookie="token=' '";
      document.cookie="userInfo=' '"
    },
    setsubMsg(state, payload) {
      state.submitMsg = payload;
      console.log( "state.submitMsg==>",state.submitMsg)
    },
    
  },
  actions: {
    getUserLogin(contenxt, payload) {
      axios({
        method: "get",
        url: "http://47.115.51.185/api/userinfo",
        headers: {
          'Authori-zation':'Bearer '+payload
        }
      }).then(data => {
        contenxt.commit('loginUser',data.data.data);
        localStorage.setItem('userInfo',JSON.stringify(data.data.data) )
        document.cookie = "userInfo=" +JSON.stringify(data.data.data)

      })
    }
  },
  modules: {
  }
})
